import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/ebc',
    name: 'EBC',
    component: () => import('../views/EBC.vue')
  },
  {
    path: '/malt-calculator',
    name: 'Malt calculator',
    component: () => import('../views/Malt.vue')
  },
  {
    path: '/recarbonatation-calculator',
    name: 'Recarbonatation calculator',
    component: () => import('../views/Recarbonatation.vue')
  },
  {
    path: '/alcool-calculator',
    name: 'Alcool calculator',
    component: () => import('../views/Alcool.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
