import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    ebc: null,
    beerL: 20,
  },
  mutations: {
    updateEBC (state, ebc) {
      state.ebc = ebc
    },
    updateBeerL (state, beerL) {
      state.beerL = beerL
    },
  },
  actions: {
  },
  modules: {
  }
})
